<template>
  <header class="bg-gray-200 sticky-top">
    <div class="container">
      <h1>
        CMIP6 informed rainfall Intensity-Duration-Frequency curves for India
      </h1>
      <p>
        We are currently in the process of building documentation for the
        methodologies used in developing the IDF curves, thanks for your
        patience.
      </p>
    </div>
  </header>
  <div id="app" class="main-content">
    <div>
      <BaseMap
        :overlay-content="overlayContent"
        @update-overlay-content="updateOverlayContent"
        @polygon-clicked="updatePolygonIndex"
      />
      <LocInfo :content="overlayContent" />
      <RlInfo
        v-if="rlInfoActive"
        :index="polygonIndex"
        @close-rl-info="handleRlInfoClose"
      />
    </div>
  </div>
  <footer class="bg-gray-200">
    <div class="container">
      <p>Note:</p>
      <p>
        The interface is still in the developement phase. Please write your
        suggestions/comments to rohithan[dot]na[at]gmail[dot]com
      </p>
    </div>
  </footer>
</template>

<script>
import { ref } from "vue";
import BaseMap from "./components/BaseMap.vue";
import LocInfo from "./components/LocInfo.vue";
import RlInfo from "./components/RlInfo.vue";

export default {
  name: "App",
  components: {
    BaseMap,
    LocInfo,
    RlInfo,
  },
  setup() {
    const overlayContent = ref("");
    const polygonIndex = ref(0); // The index of the clicked polygon
    const rlInfoActive = ref(false);

    const updateOverlayContent = (newContent) => {
      overlayContent.value = newContent;
    };

    const updatePolygonIndex = (index) => {
      polygonIndex.value = index; // Update the index value
      rlInfoActive.value = true;
    };

    const handleRlInfoClose = () => {
      rlInfoActive.value = false;
    };

    return {
      overlayContent,
      updateOverlayContent,
      polygonIndex,
      rlInfoActive,
      updatePolygonIndex,
      handleRlInfoClose,
    };
  },
};
</script>

