<template>
  <div id="map" style="z-index: 0"></div>
</template>

<script>
import L from "leaflet";
import { onMounted } from "vue";
import statesGeojson from "@/assets/india_boundary.geojson";
import gridsGeojson from "@/assets/data_grids.geojson";

export default {
  name: "BaseMap",
  props: ["overlayContent"],

  setup(_, { emit }) {
    let thebasemap;

    onMounted(() => {
      thebasemap = L.map("map").setView([20.5937, 78.9629], 5);

      L.tileLayer("https://tile.openstreetmap.org/{z}/{x}/{y}.png", {
        maxZoom: 19,
        attribution:
          '&copy; <a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a>',
      }).addTo(thebasemap);

      const defaultStyle = {
        fillColor: "transparent",
        fillOpacity: 0,
        opacity: 0,
      };

      const highlightStyle = {
        color: "green",
        fillOpacity: 0,
        weight: 2,
        opacity: 1,
      };

      L.geoJSON(statesGeojson, {
        style: {
          color: "red",
          weight: 2,
          opacity: 1,
        },
      }).addTo(thebasemap);

      L.geoJSON(gridsGeojson, {
        style: defaultStyle,
        onEachFeature: function (feature, layer) {
          layer.on({
            mouseover: function () {
              layer.setStyle(highlightStyle);
              const newContent = feature.properties;
              emit("update-overlay-content", newContent);
            },
            mouseout: function () {
              layer.setStyle(defaultStyle);
            },
            click: function () {
              emit("polygon-clicked", gridsGeojson.features.indexOf(feature)); // Emit the updated index value
            },
          });
        },
      }).addTo(thebasemap);
    });
  },
};
</script>

<style>
#map {
  height: 100vh;
}
</style>
