<template>
  <div class="chart-container" ref="chart"></div>
</template>

<style>
.chart-container {
  margin: 5px 0 0 5px;
}
</style>

<script>
import { onMounted, ref, watch } from "vue";
import Plotly from "plotly.js-dist-min";

export default {
  name: "LineChart",
  props: {
    idfData: {
      type: Object,
      required: true,
    },
  },
  setup(props) {
    const chart = ref(null);

    onMounted(() => {
      updateChart();
    });

    watch(
      () => props.idfData,
      () => {
        updateChart();
      }
    );

    const updateChart = () => {
      const dValues = Array.from({ length: 24 }, (_, index) => index + 1);

      const filledAreaTrace1 = {
        x: dValues.concat(dValues.slice().reverse()),
        y: props.idfData.iValues95.concat(
          props.idfData.iValues5.slice().reverse()
        ),
        fill: "toself",
        fillcolor: "rgba(173, 216, 230, 0.5)", // Light blue with 50% transparency
        line: { color: "rgba(255, 255, 255, 0)", shape: "spline" },
        mode: "lines",
        name: "<b>95 percentile</b>",
        hoverinfo: "skip", // Disable hoverinfo for the filled area
      };

      const filledAreaTrace2 = {
        x: dValues.concat(dValues.slice().reverse()),
        y: props.idfData.iValues90.concat(
          props.idfData.iValues10.slice().reverse()
        ),
        fill: "toself",
        fillcolor: "rgba(173, 216, 230, 0.7)", // Light blue with 70% transparency
        line: { color: "rgba(255, 255, 255, 0)", shape: "spline" },
        mode: "lines",
        name: "<b>90 percentile</b>",
        hoverinfo: "skip", // Disable hoverinfo for the filled area
      };

      const lineTrace = {
        x: dValues,
        y: props.idfData.iValues50,
        type: "scatter",
        mode: "lines",
        name: "<b>Ensemble mean</b>",
        line: { color: "navy", shape: "spline" }, // Set the line color to navy blue
        hovertemplate:
          "<b>Ensemble mean</b><br>" +
          "<b>duration: %{x} h</b><br>" +
          "<b>intensity: %{y:.2f} mm/h</b><br>",
        hoverlabel: {
          namelength: 0, // Set namelength to 0 to hide the secondary bit in the tooltip
          bgcolor: "white",
          font: { color: "black" },
        },
      };

      const lineTraceObs = {
        x: dValues,
        y: props.idfData.iValuesObs,
        type: "scatter",
        mode: "lines",
        name: "<b>Observed</b>",
        line: { color: "red", shape: "spline" }, // Set the line color to navy blue
        hovertemplate:
          "<b>Observed</b><br>" +
          "<b>duration: %{x} h</b><br>" +
          "<b>intensity: %{y:.2f} mm/h</b><br>",
        hoverlabel: {
          namelength: 0, // Set namelength to 0 to hide the secondary bit in the tooltip
          bgcolor: "white",
          font: { color: "black" },
        },
      };

      const data = [
        filledAreaTrace1,
        filledAreaTrace2,
        lineTrace,
        lineTraceObs,
      ];
      const layout = {
        title: "<b>Rainfall IDF CMIP6 for INDIA</b>", // Add the title here
        showlegend: true,
        hovermode: "closest",
        legend: {
          x: 0.73, // Set x position of legend to 0.8 (normalized scale)
          y: 0.98, // Set y position of legend to 0.98 (normalized scale)
          bordercolor: "grey", // Set the border color of the legend to grey
          borderwidth: 1, // Set the border width of the legend to 1
          font: { weight: "bold" }, // Set legend font weight to bold
        },
        margin: {
          l: 50, // Left margin
          r: 50, // Right margin
          t: 50, // Top margin
          b: 50, // Bottom margin
        },
        xaxis: {
          title: "<b>Duration (h)</b>",
          mirror: "ticks",
          linecolor: "grey75", // Set x-axis line color
        },
        yaxis: {
          title: "<b>Rainfall intensity (mm/h)</b>",
          mirror: "ticks",
          linecolor: "grey75", // Set y-axis line color
        },
      };

      Plotly.newPlot(chart.value, data, layout);
    };

    return { chart };
  },
};
</script>

<style>
.chart-container {
  margin: 5px;
}
</style>
