<template>
  <div
    id="main-container-top"
    class="position-fixed top-50 start-50 translate-middle bg-white rounded p-3"
    style="z-index: 40"
  >
    <button
      type="button"
      class="btn-close position-absolute top-0 end-0"
      style="margin-top: 3px; margin-right: 3px"
      aria-label="Close"
      @click="$emit('close-rl-info')"
    ></button>
    <template v-if="isData">
      <div class="container">
        <div id="main-container" class="d-flex flex-column align-items-center">
          <div class="input-container">
            <div class="input-group">
              <label for="scenario" class="label">Scenario:</label>
              <select
                id="scenario"
                v-model="selectedScenario"
                class="form-select w-auto"
              >
                <option
                  v-for="scenario in scenarioOptions"
                  :key="scenario"
                  :value="scenario"
                >
                  {{ scenario }}
                </option>
              </select>
            </div>
            <div class="input-group">
              <label for="timePeriod" class="label">Time Period:</label>
              <select
                id="timePeriod"
                v-model="selectedTimePeriod"
                class="form-select w-auto"
              >
                <option
                  v-for="period in timePeriodOptions"
                  :key="period"
                  :value="period"
                >
                  {{ period }}
                </option>
              </select>
            </div>
            <div class="input-group">
              <label for="returnPeriod" class="label">Return Period:</label>
              <select
                id="returnPeriod"
                v-model="selectedReturnPeriod"
                class="form-select w-auto"
              >
                <option
                  v-for="rperiod in returnPeriodOptions"
                  :key="rperiod"
                  :value="rperiod"
                >
                  {{ rperiod }}-Years
                </option>
              </select>
            </div>
          </div>
          <div class="hr-container">
            <hr />
          </div>
          <div class="plot-container">
            <idf-plot :idf-data="idfData"></idf-plot>
          </div>
        </div>
      </div>
    </template>
    <template v-else>
      <div
        class="container position-relative mt-1"
        style="margin-bottom: -15px"
      >
        <p>{{ comment }}</p>
      </div>
    </template>
  </div>
</template>

<script>
import { computed, ref, watch } from "vue";
import rl2yrData from "@/assets/rl_2yr_cmip6.json";
import rl2yrObs from "@/assets/rl_2yr_obs.json";
import IdfPlot from "./IdfPlot.vue";

export default {
  name: "RlInfo",
  emits: ["close-rl-info"],
  components: {
    IdfPlot,
  },
  props: ["index"],
  setup(props) {
    const gridRlInfo = computed(() => rl2yrData?.[props.index]);

    const isData = computed(() => gridRlInfo.value?.isData ?? false);
    const comment = computed(() => {
      if (!isData.value) {
        return gridRlInfo.value?.Comment ?? "";
      } else {
        return "";
      }
    });

    const dValues = Array.from({ length: 24 }, (_, index) => index + 1);
    const scenarioOptions = [
      "Historical",
      "SSP126",
      "SSP245",
      "SSP370",
      "SSP585",
    ];
    const returnPeriodOptions = [2, 5, 10, 25, 50, 100];
    const selectedReturnPeriod = ref(2);

    const selectedScenario = ref("SSP245");
    const selectedTimePeriod = ref("");

    const timePeriodOptions = computed(() => {
      if (selectedScenario.value === "Historical") {
        return ["1975-2014"];
      } else {
        return ["2020-2049", "2050-2099"];
      }
    });

    watch(
      selectedScenario,
      (newScenario) => {
        if (isData.value) {
          selectedTimePeriod.value =
            newScenario === "Historical"
              ? "1975-2014"
              : timePeriodOptions.value[0];
        }
      },
      { immediate: true }
    );

    const idfData = computed(() => {
      if (isData.value) {
        const currentIndex = props.index;
        const gridRlInfo = rl2yrData?.[currentIndex];
        const allRvalues =
          gridRlInfo.thedata[selectedScenario.value][selectedTimePeriod.value];

        const [rValue5, rValue10, rValue50, rValue90, rValue95] =
          allRvalues?.map(parseFloat) ?? [null, null, null, null, null];
        const rValuesObs = rl2yrObs?.[props.index].RL;

        const c = 0.71;

        const t = selectedReturnPeriod.value;
        const iValues5 = dValues.map((d) => calculateI(c, t, d, rValue5));
        const iValues10 = dValues.map((d) => calculateI(c, t, d, rValue10));
        const iValues50 = dValues.map((d) => calculateI(c, t, d, rValue50));
        const iValues90 = dValues.map((d) => calculateI(c, t, d, rValue90));
        const iValues95 = dValues.map((d) => calculateI(c, t, d, rValue95));
        const iValuesObs = dValues.map((d) => calculateI(c, t, d, rValuesObs));

        return {
          iValuesObs,
          iValues5,
          iValues10,
          iValues50,
          iValues90,
          iValues95,
        };
      } else {
        return { iValues: [] };
      }
    });

    const calculateI = (c, t, d, rValue) => {
      return (
        ((c * Math.pow(t, 0.2)) / Math.pow(d, 0.71)) * Math.pow(rValue, 0.33)
      );
    };

    return {
      idfData,
      selectedScenario,
      selectedTimePeriod,
      scenarioOptions,
      timePeriodOptions,
      returnPeriodOptions,
      selectedReturnPeriod,
      isData,
      comment,
    };
  },
};
</script>

<style scoped>
#main-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input-container {
  display: flex;
}

.input-group {
  display: grid;
  grid-template-columns: auto auto;
  align-items: center;
}

.label {
  margin-left: 5px;
  margin-right: 5px;
  white-space: nowrap;
}

.hr-container {
  width: 100%;
}

.hr-container hr {
  border: none;
  border-top: 1px solid black;
  margin: 20px 0;
}
</style>
